intrcn: "높이는 1,058m이다. 태백산맥에서 남서 방향으로 뻗어 나온 소백산맥 줄기 가운데 위치한다. 최고봉인 천왕봉을 중심으로 비로봉·길상봉·문수봉·보현봉·관음봉·묘봉·수정봉 등 8개의 봉과 문장대·입석대·경업대·배석대·학소대·신선대·봉황대·산호대 등 8개의 대가 있다. 대부분의 봉과 대가 보은 쪽에 있는 반면, 은폭동을 제외한 용유동계곡·쌍룡폭포·오송폭포·장각폭포·옥량폭포·용화온천 등은 상주 쪽에 있다. 속리산에는 천연기념물인 정이품송·망개나무 등 1,055종의 식물이 서식하고 있다. 또한 까막딱따구리·하늘다람쥐 등 희귀동물을 포함하여 1,831종의 동물이 서식하고 있다.봄에는 산벚꽃, 여름에는 무성한 녹음, 가을에는 아름다운 단풍, 겨울에는 설경으로 계절마다 장관을 이루며 등산로도 잘 닦여 있어 많은 등산객들이 찾는다. 박대천의 지류인 화양천을 따라 좌우로 산재한 경승지를 화양구곡이라 하는데, 한줌의 흙도 보이지 않는 흰 반석에 맑은 물이 흐르고 있으며 울창한 숲과 조화를 이룬 모습이 장관이다. 화양구곡은 경천벽·운영담·읍궁암·금사담·첨성대·청운대·와룡암·학소대·파관등으로 이루어져 있으며 선유동구곡은 선유동문·경천벽·학소암·연단로·와룡폭·난가대·기국암·구암·은선암 등으로 이루어져 있다. 출처: [네이버 지식백과] 속리산 [俗離山] (한국민족문화대백과, 한국학중앙연구원)"
